import React from 'react';
import { useRouteError } from 'react-router-dom';

export const ErrorPage:React.FC = () => {

  const error = useRouteError();
  let message = "-   .     .";

  if(error instanceof Error && error.message) {
      message = error.message;
  }

  return (
    <div>{message}</div>
  );
};
